*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,body,#root{
  height: 100%; 
  font-size: 14px;
  line-height: 1.47;
  font-family: Roboto,Arial, Helvetica, sans-serif;
  background : #dcdcdc;
}

.wrapper{
  height: 100%;
  width: 100%; 
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;

  &Inner{
    display: flex; 
    width: 650px;
    background: #f6f9ff;
    border-radius: 4px;
    padding: 15px;
    flex-direction: column;

    .heading{
      text-align: center;
      margin-bottom: 10px;
      color: #333;
    }
    
    form{

      flex: 1;
      display:flex;
      flex-direction: column;
      
      label {
        font-size: 14px;
        font-weight: 700;
        font-stretch: normal;
        font-style: normal;
        line-height: normal;
        letter-spacing: normal;
        color: #6a7ea5;
        margin-bottom: 2px;
      }

      input{
        width: 100%; 
        height: 28px;
        box-sizing: border;
        border-radius: 4px;
        border: solid 1px #dce8ef;
        background-color: #fff;  
        font-weight: 400;
        font-stretch: normal;
        font-style: normal;
        line-height: normal;
        letter-spacing: normal;
        color: #333;
        padding: 0 20px;
      }

      .select-control{
         margin-bottom: 10px;

         
      }

      .form__input--control{
        display: flex;

        .input-control{
          flex: 1;
          margin-right: 10px;

          input{
            padding: 20px;
            outline-color: rgb(83, 156, 252);
          }
        }
        
        .input-control:last-child{
          margin-right: 0;
        }
      }

      .buttonsControlWrap{
        display: flex;
        justify-content: space-evenly;
        margin: 10px;

        & > button {
          padding: 10px 20px;
          border-radius: 4px;
          box-shadow: 0 5px 8px 0 rgba(136,200,247,.39);
          border: solid 1px #1777be;
          background-color: #1492ef;
          display: flex;
          align-items: center;
          justify-content: center;
          cursor: pointer; 
          font-weight: 700;
          font-stretch: normal;
          font-style: normal;
          line-height: normal;
          letter-spacing: normal;
          color: #fff; 

          &:hover{
            box-shadow: 0 5px 8px 0 rgba(136 ,200, 247, 1); 
          }

          &.cancelBtn{
            background: rgb(250, 74, 74);
            border-color:rgb(247, 0, 0);

            &:hover{
              box-shadow: 0 5px 8px 0 rgba(250, 74, 74, 0.5);
            }
          }
        }
        

      } 
    }

    .resultBlock{
      border-radius: 4px;
      background: #77849f url(../images/tick-2.png) left center / contain no-repeat;
      padding: 15px 45px;
      margin-top: 20px;
      display: flex;
      align-items: center;

      .resultIcon{
        margin-right: 7px;
        margin-top: 2px;
      }

      .resultText{ 
        font-size: 16px;
        font-weight: 400;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.38;
        letter-spacing: normal;
        color: #fff;
      }
    }
  }

}

